import { Component, OnInit } from '@angular/core';

import { CourseService } from '../../angular-services/course.service';
import { Course } from '../../classes/course';

@Component({
  selector: 'app-list-courses-admin',
  templateUrl: './list-courses.component.html',
  styleUrls: ['./list-courses.component.css']
})
export class ListCoursesComponent implements OnInit {

  courses: Course[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private courseService: CourseService) { }

  ngOnInit() {
    this.getCourses();
  }

  getCourses() {
    this.courseService.getCourses().subscribe(
      response => {
        if (response !== undefined) {
          this.courses = response;
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao carregar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  removeCourse(course) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.courses = this.courses.filter(c => c !== course);
      this.courseService.removeCourse(course._key).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Curso excluído com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao carregar os dados!';
            this.alert.show = true;
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
